body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color {
    background: #74ebd5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ACB6E5, #74ebd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.glowna-grid {
    background: #74ebd5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ACB6E5, #74ebd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.projekty-grid {
    display: flex; 
}
.projekty-link {
    text-decoration: none;
    color: darkgrey;
}
.omnie-body {
    background: #74ebd5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ACB6E5, #74ebd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    margin: auto;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 1em;
}
.omnie-grid {
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;
}
.omnie-grid h2 {
    font-family: 'Anton', sans-serif;
}
.omnie-grid p {
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;
}
.omnie-grid hr {
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}
.cvright {
    background: #27221F;
    color: white;
    padding: 2em;
}
.contact-body {
    background: #74ebd5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ACB6E5, #74ebd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    margin: auto;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
}
.contact-grid {
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;
}
.contact-grid h2 {
    font-family: 'Anton', sans-serif;
}
.contact-grid p {
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;
}
.contact-grid hr {
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}
.contact-list i {
    font-size: 66px;
    padding-right: 5rem;
    
}
.avatar-img {
    height: 250 px;
    padding-top: 5em;
}
.banner-text {
    background-color: black;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}
.banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: white;
}
.banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}
.banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
}
.social-links {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}
.social-links i {
    color: white;
    font-size: 5em;
    padding: 8px;
}
